import React from 'react';

import BlogContent from './blogcontent';
import BlogHeader from './blogheader';
import blog3_img from '../../assets/img/team/himanshu_khosla.jpeg';



export default class HumansofFC extends React.Component{
    render(){
        return(
            <div>
                <BlogHeader  heading="Humans of FoodCham"/>

                <BlogContent post_title="Himanshu Khosla"  src={blog3_img} pub_date="May 16, 2018" category="Humans of FC" brief="Humans of Foodcham have always been there as a pillar for the stability of Foodcham at all times. All the team members, mentors and well wishers have made it possible for the company to stand as it is in the corporate world. We at Foodcham feel that each members contribution remains important both in their field of work and journey. One of the most important hidden talent of Foodcham is Himanshu Khosla. Full stack developer working at code what's right with full dedication and commitment.Here is a what Himanshu has to say about his journey:" link="/Blogs/HumansofFC/Himanshu_khosla">
                </BlogContent>   

            </div>
        );
    }
}


